body {
  margin: 10;
  padding: 0;
  font-family: sans-serif;
}

h1, h3 {
  color: gray;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}

.wrapper {
  color: gray;
  padding-top: 5px;
  font-size: 15px;
}

.navbar-nav span[role=button] {
  padding: 15px;
  display: inline-block;
  line-height: 20px;
}

.bg-img {
  /* The image used */
  background-image: url("https://cdn-images-1.medium.com/max/800/1*GtD40g2XNGNepe5Ss_zMsA.jpeg");

  /* Control the height of the image */
  min-height: 500px;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.userstyle {
  /* The image used */
  background-image: url("http://s3.amazonaws.com/anmolmehta/wp-content/uploads/20180118214508/3-Ways-to-Simplify-Your-Life.jpg");

  /* Control the height of the image */
  min-height: 500px;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}


